<script>
import axios from 'axios';
import confirm from "../../../../owproject/src/front/src/components/popup/confirm_Check_Delete";

export default {
  components: {
    'modal-delete': confirm
  },
  data() {
    return {
      diaryDatas: Object,
      modalStatus: {
        delete: false,
      }
    }
  },
  mounted: function () {
        axios.get('api/diary')
            .then(response => {
              console.log(response);
              this.diaryDatas = response.data
            })
            .catch(function (error) {
              console.log(error);
            });
  },
  methods: {
    removeItem(diaryData) {
      // console.log(diaryData);
      this.checkRemove();
    },
    checkRemove() {
      this.modalStatus.delete = true;
    },
    closeDeleteModal(closeit) {
      // console.log(closeit);
      this.modalStatus.delete = false;
    }
  }
}

</script>